QACpC,gBAAgB;;QAEhB;YACI,iCAAiC;YACjC,yBAAyB;QAC7B;IACJ;AACJ;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,iCAAiC;AACrC;AACA,2CAA2C;;AAE3C;IACI,YAAY;IACZ,eAAe;IACf,QAAQ;IACR,WAAW;IACX,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,yCAAyC;IACzC,2BAA2B;AAC/B;;AAEA;IACI,uBAAuB;IACvB,yEAAyE;IACzE,YAAY;IACZ,WAAW;IACX,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,qBAAqB;AACzB;AACA,8CAA8C;;AAE9C;IACI;QACI,aAAa;QACb,kBAAkB;QAClB,6BAA6B;QAC7B,OAAO;QACP,WAAW;QACX,YAAY;QACZ,kCAAkC;QAClC,aAAa;QACb,YAAY;QACZ,qCAAqC;QACrC,yBAAyB;QACzB,uBAAuB;IAC3B;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,UAAU;QACV,UAAU;IACd;;IAEA;QACI,YAAY;QACZ,YAAY;QACZ,eAAe;QACf,0BAA0B;QAC1B,SAAS;QACT,gBAAgB;QAChB,kBAAkB;IACtB;;IAEA;QACI,mBAAmB;IACvB;;IAEA;QACI,kBAAkB;QAClB,aAAa;IACjB;;IAEA;QACI,0BAA0B;QAC1B,gDAAgD;IACpD;;IAEA,qCAAqC;IACrC;QACI,kBAAkB;QAClB,2BAA2B;QAC3B,kBAAkB;QAClB,YAAY;;IAEhB;;IAEA;QACI,aAAa;IACjB;;;IAGA,0CAA0C;IAC1C;QACI,UAAU;QACV,YAAY;IAChB;;IAEA;QACI,WAAW;QACX,eAAe;QACf,aAAa;IACjB;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,8CAA8C;IAClD;AACJ;;AAEA;IACI;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,4DAA4D;QAC5D,cAAc;;IAElB;;IAEA;QACI,kBAAkB;QAClB,cAAc;KACjB;AACL","sourcesContent":["*, *::before, *::after {\n    padding: 0;\n    margin: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    -webkit-font-smoothing: antialiased;\n    line-height: 1.5;\n}\n\nimg, picture, video, canvas, svg {\n    display: block;\n    max-width: 100%;\n}\n\ninput, button, textarea, select, dialog, form, label {\n    font: inherit;\n    color: inherit;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n}\n\np {\n    text-wrap: pretty;\n}\n\n#root, #__next {\n    isolation: isolate;\n}\n\nul, li {\n    list-style-type: none;\n}\n\na {\n    text-decoration: none;\n}\n\nbutton {\n    cursor: pointer;\n    border: none;\n    transition: background-color 0.2s ease-in-out;\n}\n\n*:not(i) {\n    font-family: quicksand;\n}\n/***********VARIABLES**************/\n:root {\n    --header-size: 100px;\n    --sidebar-size: 225px;\n    --header-font-size: clamp(0.8rem, 0.8rem + 0.75vw, 25px);\n\n    --header-size-small: calc(var(--header-size) / 1.5);\n    \n\n    --clr-cards: #172a46;\n    --clr-aside: #172a46;\n    --clr-content: #0a192f;\n    --clr-success: #4CAF50;\n    --clr-success-hover: #81C784;\n    --clr-failure: #F44336;\n    --clr-failure-hover: #E57373;\n    --clr-card-text: white;\n    --clr-text: #a4b0cf;\n    --clr-action-btn: #172a46;\n}\n/*************************/\nbody {\n    min-height: 100dvh;\n    display: grid;\n    grid-template-rows: var(--header-size) 1fr;\n    grid-template-columns: var(--sidebar-size) 1fr;\n    color: var(--clr-text);\n}\n\nheader {\n    grid-area: 1 / 2 / 2 / 3;\n}\n\n\naside {\n    grid-area: 1 / 1 / -1 / 2;\n}\n\n#content {\n    grid-area: 2 / 2 / -1 / -1;\n    background-color: var(--clr-content);\n    --gap-local: calc(var(--gap) / 2);\n}\n\nheader, aside {\n    position: sticky;\n    top: 0;\n    left: 0;\n}\n\n.smallscreen-header { \n    display: none;\n}\n\n#dropdownBtn {\n    display: none;\n}\n/**************HEADER***************/\n\nheader {\n    z-index: 5;\n    background-color: var(--clr-action-btn);\n    box-shadow: 0px 0px 16px 16px rgba(0,0,0,0.4);\n}\n\nnav {\n    height: 100%;\n    --border: 1px solid black;\n}\n\n.header-buttons-container {\n    display: flex;\n    height: 100%;\n}\n\nnav li:not(:has(i)) {\n    flex: 1;\n    border-right: var(--border);\n\n    &:first-child {\n        border-left: var(--border);\n    }\n}\n\n.action-button {\n    height: 100%;\n    width: 100%;\n    font-size: var(--header-font-size);\n    font-weight: 900;\n    background-color: var(--clr-cards);\n}\n\n.action-button:hover {\n    background-color: var(--clr-text);\n    color: var(--clr-action-btn);\n}\n/**************SIDEBAR***************/\naside {\n    z-index: 6;\n    height: 100dvh;\n    background-color: var(--clr-aside);\n    align-self: start;\n    display: grid;\n    grid-template-rows: var(--header-size) 1fr;\n    padding-left: 1.2em;\n    box-shadow: 0px 0px 16px 16px rgba(0,0,0,0.4);\n    gap: 1em;\n}\n\naside h1 {\n    font-size: 2.5rem;\n    display: flex;\n    align-items: center;\n}\n\n.sidebar-buttons-container {\n    display: flex;\n    flex-direction: column;\n}\n\n.sidebar-button {\n    /* width: fit-content; */\n    height: 3em;\n    font-size: 1.2em;\n    transition: transform 0.075s ease-in-out, filter 0.075s ease-in-out;\n}\n\n.sidebar-button:hover, .sidebar-button.active {\n    transform: translate(10%, -5%) scale(1.1);\n    cursor: pointer;\n}\n\n.sidebar-button:hover {\n    filter: drop-shadow(0 0 10px white);\n}\n\n/**************CONTENT:CARDS***************/\n\n#content { \n    --gap: 6rem;\n    --shadow: 0px 0px 32px 16px rgba(0,0,0,0.4);\n }\n\n.card.tasks::before { background-color: var(--clr-text); }\n.card.missions::before { background-color: var(--clr-success); }\n.card.offenses::before { background-color: var(--clr-failure); }\n\n.card.tasks:hover { border: 2px solid var(--clr-text); }\n.card.missions:hover { border: 2px solid var(--clr-success); }\n.card.offenses:hover { border: 2px solid var(--clr-failure); }\n\n/* Cards */\n\n#content:has(.card) {\n    z-index: 1;\n    display: grid;\n    padding: var(--gap);\n    gap: var(--gap);\n    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n    grid-auto-rows: max-content;\n}\n\n.card {\n    --border-radius: 16px;\n    --anim-time: 0.2s;\n    font-size: 90%;\n\n    position: relative;\n    background-color: var(--clr-cards);\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    padding: 2em;\n    gap: 1em;\n    border-radius: var(--border-radius);\n    border: 2px solid transparent;\n    box-shadow: var(--shadow);\n    transition: box-shadow var(--anim-time) ease-in-out, border var(--anim-time) ease-in-out;\n}\n\n.card:hover {\n    box-shadow: 0px 0px 32px 28px rgba(0,0,0,0.4);\n    &::before { left: 0; }\n}\n\n.card::before {\n    position: absolute;\n    content: '';\n    z-index: -1;\n    top: 0%;\n    left: -1.5%;\n    height: 100%;\n    width: 100%;\n    border-radius: var(--border-radius);\n    transition: transform var(--anim-time) ease-in-out;\n}\n\n.card-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    & p { font-weight: 900;}\n\n    & h1 {\n        font-size: clamp(1.5rem, 1.3rem + 0.75vw, 3rem);\n        word-break: break-all;\n    }\n}\n\n.card > p {\n    font-size: 1.1em;\n}\n\n.card-buttons-container {\n    display: flex;\n    justify-content: center;\n    align-self: center;\n    margin-top: 2em;\n    gap: 1em;\n    width: 100%;\n    min-height: 2.5em;\n}\n\n.card-button {\n    width: max(100px, 35%);\n    text-align: center;\n    border-radius: 16px;\n    font-weight: 900;\n    font-size: clamp(0.8em, 0.8em + 0.2vw, 1.5em);\n    padding: 0.7em 0;\n    color: var(--clr-card-text);\n}\n\n.complete-mission-button, .complete-offense-button {\n    width: 50%;\n    align-self: center;\n}\n\n.success {\n    background-color: var(--clr-success);\n}\n\n.success:hover {\n    background-color: var(--clr-success-hover);\n}\n\n.failure {\n    background-color: var(--clr-failure);\n}\n\n.failure:hover {\n    background-color: var(--clr-failure-hover);\n}\n\n.corner-buttons-container {\n    display: flex;\n    justify-content: flex-end;\n    gap: 1rem;\n    width: 100%;\n}\n\n.corner-button {\n    height: 28px;\n    cursor: pointer;\n    filter: invert(83%) sepia(7%) saturate(1021%) hue-rotate(187deg) brightness(84%) contrast(91%);\n    transition: transform 0.15s ease-in-out, filter 0.15s ease-in-out;\n\n}\n\n.corner-button:hover {\n    transform: scale(1.2);\n    filter: invert(83%) sepia(7%) saturate(1021%) hue-rotate(187deg) brightness(84%) contrast(91%) drop-shadow(0 0 10px white);\n}\n\n/*******CONTENT:STATS************/\n#content:has(.stats-embed) {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    min-width: 600px;\n}\n\n#statsTitle {\n    position: relative;\n    width: 100%;\n    margin-bottom: calc(var(--gap-local) / 2);\n    font-size: clamp(1rem, 1.3rem + 0.5vw, 2rem);\n}\n\n.stats-embed {\n    background-color: var(--clr-cards);\n    min-height: 60%;\n    width: max(420px, 70%);\n    border-radius: 16px;\n    padding: var(--gap-local);\n    box-shadow: var(--shadow);\n    display: flex;\n    flex-direction: column;\n}\n\n#stats {\n    flex: 1;\n    box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.2);\n    background-color: var(--clr-content);\n    border-radius: 16px;\n    padding: 2em;\n}\n\ntd {\n    padding: 1em;\n    font-weight: 900;\n    font-size: clamp(0.7rem, 0.7rem + 0.55vw, 1.7rem);\n}\n\ntd:first-child::before {\n    content: \"\\0025CB\\000020\";\n}\n\n/*******CONTENT:SPEND************/\n\n#content:has(.spend-embed) {\n    display: grid;\n    place-items: center;\n}\n\n.spend-embed {\n    background-color: var(--clr-cards);\n    border-radius: 16px;\n    padding: var(--gap-local);\n    box-shadow: var(--shadow);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 1em;\n}\n\n.spend-embed * {\n    width: 100%;\n}\n\n.points-form {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.points-container {\n    margin: 0 auto;\n}\n\n#spendFormBtn {\n    margin-top: 2em;\n}\n\n/************FORM****************/\ndialog {\n    --gap: 1.5rem;\n\n    place-self: center;\n    background-color: var(--clr-cards);\n    border-radius: 10px;\n    padding: var(--gap);\n\n    border: none;\n    transform: scale(0);\n    visibility: hidden;\n    transition: transform 0.3s ease-in-out, visibility 0s 0.3s;\n\n    & > h1 { /* Modal header */\n        display: grid;\n        place-self: center;\n        margin-bottom: var(--gap);\n    }\n\n    &.show {\n        transform: scale(1);\n        visibility: visible;\n        transition: transform 0.3s ease-in-out, visibility 0s 0s;\n        border: 1px solid var(--clr-text);\n\n    }\n}\n\ndialog form {\n    display: flex;\n    flex-direction: column;\n    gap: var(--gap);\n    font-size: 1em;\n}\n\n.name, .desc {\n    display: flex;\n    flex-direction: column;\n}\n\ninput {\n    outline: none;\n    border: none;\n    border-radius: 16px;\n    background-color: var(--clr-content);\n    padding: 0.3em 1em;\n    font-size: 1em;\n}\n\n.points-general {\n    display: flex;\n    flex-direction: row;\n    gap: 1em;\n\n    & * { /* Reward and Penalty divs */\n        flex: 1;\n        display: flex;\n        flex-direction: column;\n    }\n}\n\n.form-buttons {\n    display: flex;\n    gap: 1em;\n\n    & button {\n        flex: 1;\n        border-radius: 16px;\n        background-color: var(--clr-content);\n        padding: 0.3em 0;\n\n        &:hover {\n            background-color: var(--clr-text);\n            color: var(--clr-content);\n        }\n    }\n}\n\ninput:valid {\n    border: 1.5px solid var(--clr-card-text);\n}\n\ninput:invalid {\n    border: 1.5px solid red;\n}\n\n::backdrop {\n    background-color: rgba(0,0,0,0.6);\n}\n/************FEEDBACK STRIP****************/\n\n.feedback-container {\n    z-index: 900;\n    position: fixed;\n    right: 0;\n    bottom: 10%;\n    min-width: 300px;\n    color: white;\n    text-align: center;\n    font-weight: 900;\n    box-shadow: 0 0 16px 16px rgba(0,0,0,0.4);\n    transform: translateX(150%);\n}\n\n.feedback {\n    background-color: green;\n    transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;\n    height: 100%;\n    width: 100%;\n    font-size: 1rem;\n    padding: 0.75em 1.25em;\n}\n\n.feedback.visible {\n    transform: translateX(-150%);\n}\n\n.feedback.fail {\n    background-color: red;\n}\n/**************MEDIA QUERIES******************/\n\n@media (max-width: 830px) {\n    aside {\n        display: none;\n        position: absolute;\n        top: var(--header-size-small);\n        left: 0;\n        width: 100%;\n        height: auto;\n        background-color: va